header {
	 background-image: url("../../../images/banner.jpeg");
	 background-size: cover;
	 background-position: center;
	 width: 100%;
	 margin-bottom: 7.5px;
}
 .header-container {
	 background-color: rgba(224, 224, 224, 0.65);
	 padding: 75px 10px 50px 10px;
	 text-align: center;
}
 .header-container h1 {
	 margin-bottom: 50px;
	 font-size: 30px;
}
 .button-wide {
	 padding: 10px 15px;
	 margin: 5px 0;
	 background: #f9be01;
	 border-radius: 5px;
}
 
 @media all and (min-width: 600px) {
	 .header-content {
		 max-width: 592px;
		 margin: auto;
	}
}

@media all and (min-width: 960px) {
    header {
        margin-bottom: 30px;
    }

    .header-container {
        padding: 100px 10px 50px 10px;
    }

    .header-container h1 {
        font-size: 35px;
        margin-bottom: 75px;
    }
	}