.poll-container {
  width: 100%;
  max-width: 600px;
  margin: auto;
}

.sub-nav {
  width: 100%;
  background-color: #00bf6f;
  display: flex;
  flex-direction: column;
  padding: 22.5px 0 5px 0;
  margin-bottom: 15px;
}

.sub-nav h2 {
  text-align: center;
  margin-bottom: 20px;
  color: #fff;
}

.sub-menu {
  list-style-type: none;
  display: flex;
  justify-content: center;
  column-gap: 10px;
}

.sub-button a {
  display: block;
  padding: 10px 15px;
  color: #000;
  font-weight: normal;
  text-decoration: none;
  background-color: #fff;
  border-radius: 5px;
  font-size: 14px;
}

.yellow-button a {
  background: #f9be01;
}

.poll-question {
  width: 95%;
  margin: auto;
  margin-bottom: 10px;
  border-radius: 5px;
  box-shadow: 0px 0px 3px 0px rgba(66, 68, 90, 1);
}

.poll-question-content {
  padding: 10px;
}

.poll-question-content input[type="text"] {
  width: 100%;
  border: none;
  padding: 10px 0;
  font-size: 16px;
  margin-bottom: 15px;
}

.poll-question-content input[type="text"]:focus {
  outline: none;
  border-bottom: 1px solid #00bf6f;
}

.poll-question-content label {
  margin-bottom: 10px;
}

.question-content {
  display: flex;
  margin-bottom: 10px;
}

.answers {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.question-image {
  flex: 1;
  max-height: 150px;
  max-width: 150px;
}

.question-image img {
  width: 100%;
  height: 100%;
  border-radius: 5px;
}

.question-menu {
  width: 100%;
  background-color: #f9be01;
  padding: 10px;
  display: flex;
  column-gap: 10px;
  align-items: stretch;
}

.question-menu button {
  padding: 10px 5px;
  border: none;
  border-radius: 5px;
}

.type-answer select {
  height: 100%;
  border: none;
  padding: 10px 5px;
  border-radius: 5px;
}

.type-answer select option {
  padding: 10px 5px;
  border-radius: 5px;
}

.add-image {
  display: flex;
  align-items: center;
}

.fa-image {
  font-size: 30px;
  color: #00bf6f !important;
}

.add-question {
  width: 95%;
  margin: auto;
  margin-bottom: 10px;
  border-radius: 5px;
  box-shadow: 0px 0px 3px 0px rgba(66, 68, 90, 1);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
}

.add-question button {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  box-shadow: 0px 0px 3px 0px rgba(66, 68, 90, 1);
}
