.page-container-login {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.form {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.form-content {
  height: 100%;
  width: 100%;
  max-width: 300px;
  margin: auto;
  padding: 15px 20px;
  text-align: center;
}

.form-content h2 {
  font-size: 20px;
  margin-bottom: 15px;
}

.form-content input {
  width: 100%;
  padding: 10px 0;
  margin-bottom: 10px;
  border: none;
  border-bottom: 1px solid #00bf6f;
}

.form-content input:focus {
  outline: none;
}

.form-content button {
  padding: 10px 15px;
  background-color: #f9be01;
  border: none;
  border-radius: 5px;
  font-weight: bold;
  margin-top: 10px;
}

.error {
  margin: 40px 0;
  color: red;
}

.info {
  margin-bottom: 20px;
  text-align: center;
}
