.about {
  padding: 15px 25px;
  text-align: justify;
  max-width: 450px;
  margin: auto;
}
.about p {
  margin-bottom: 22.5px;
}
.about img {
  margin-bottom: 22.5px;
}
.about-text {
  display: none;
}
.about-1 {
  width: 100%;
  padding: 0 0 5px 0;
  border-bottom: 2px solid #f9be01;
}
.about-2 {
  width: 100%;
  padding: 20px 0 0 0;
}
img {
  height: auto;
  width: 100%;
}
p {
  font-size: 15px;
}

@media all and (min-width: 680px) {
  .about {
    width: 95%;
    max-width: 636px;
  }

  .about-1,
  .about-2 {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }

  .about-image-1 {
    flex: 1;
    order: 1;
  }

  .about-image-2 {
    flex: 1;
    order: 2;
  }

  .about-1 .about-text-2 {
    flex: 1;
    order: 2;
    padding-left: 15px;
  }

  .about-2 .about-text-2 {
    flex: 1;
    order: 1;
    padding-right: 15px;
  }
}

@media all and (min-width: 960px) {
  .about {
    max-width: 825px;
  }

  .about-1 .about-text-1,
  .about-1 .about-text-2,
  .about-2 .about-text-1,
  .about-2 .about-text-2 {
    display: none;
  }

  .about-1,
  .about-2 {
    align-items: stretch;
  }

  .about-2 {
    padding: 50px 20px 0 0;
  }

  .about-1 .about-text {
    display: flex;
    flex: 1;
    order: 2;
    padding: 50px 20px 80px 30px;
    flex-wrap: wrap;
  }

  .about-2 .about-text {
    display: flex;
    flex: 1;
    order: 1;
    padding: 50px 30px 50px 0px;
    flex-wrap: wrap;
  }

  .about-text-p2 {
    align-self: flex-end;
  }
}
